import React, { useState } from "react";
import hashlogo from './pngghh.png'
import { useNavigate } from "react-router-dom";
function Signup() {
  const navigate = useNavigate()
  const SubmitLogin = ()=>{
    navigate('../login')
  }
  const [credentials, setcredentials] = useState({
    name : "",
    email : "",
    password : "",
    cpassword : "",
    contact : "",
    address : "",
  })
  const onchange = (e)=>{
    setcredentials({ ...credentials, [e.target.name] : e.target.value})
  }
  const handlesubmit = async(e)=>{
      e.preventDefault()
      const response = await fetch("http://localhost:5000/api/auth/createuser",{
        method : 'POST',
        headers : {
          "Content-Type": "application/json",
        },
        body : JSON.stringify({
          email : credentials.email,
          name : credentials.name,
          password : credentials.password,
          address : credentials.address,
          contact : credentials.contact
        })
      })
      const json = await response.json()
      console.log(json)
      if (json.success) {
        localStorage.setItem('authtoken', json.authtoken)
        localStorage.setItem('name',credentials.name)
        localStorage.setItem('email', credentials.email)
        navigate('/')
      }
      else{
        alert('User with these credentials already exists')
      }
  }
  return (
    <section className="bg-gray-50 dark:bg-gray-900">
      <div className="flex flex-col items-center justify-center px-6 py-8 mx-auto md:h-screen lg:py-0">
        <a
          href="/"
          className="flex items-center mb-6 text-2xl font-semibold text-gray-900 dark:text-white"
        >
          <img
            className="w-50 h-20 mr-2"
            src={hashlogo}
            alt="logo"
          />
          HashLimo
        </a>
        <div className="w-full bg-white rounded-lg shadow dark:border md:mt-0 sm:max-w-md xl:p-0 dark:bg-gray-800 dark:border-gray-700">
          <div className="p-6 space-y-4 md:space-y-6 sm:p-8">
            <h1 className="text-xl font-bold leading-tight tracking-tight text-gray-900 md:text-2xl dark:text-white text-center">
              Create an account
            </h1>
            <form className="space-y-4 md:space-y-6" action="#" onSubmit={handlesubmit}>
              <div>
                <label
                  for="Name"
                  className="block mb-2 text-sm font-medium text-gray-900 dark:text-white"
                >
                  Your Name
                </label>
                <input
                  type="text"
                  name="name"
                  id="name"
                  className="bg-gray-50 border border-gray-300 text-gray-900 sm:text-sm rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                  placeholder="John Appleseed"
                  required=""
                  onChange={onchange}
                  value={credentials.name}
                />
              </div>
              <div>
                <label
                  for="Email"
                  className="block mb-2 text-sm font-medium text-gray-900 dark:text-white"
                >
                  Email
                </label>
                <input
                  type="email"
                  name="email"
                  id="email"
                  placeholder="johnnyappleseed@xyz.com"
                  className="bg-gray-50 border border-gray-300 text-gray-900 sm:text-sm rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                  required=""
                  onChange={onchange}
                  value={credentials.email}
                />
              </div>
              <div>
                <label
                  for="Password"
                  className="block mb-2 text-sm font-medium text-gray-900 dark:text-white"
                >
                  Password
                </label>
                <input
                  type="password"
                  name="password"
                  id="password"
                  placeholder="••••••••"
                  className="bg-gray-50 border border-gray-300 text-gray-900 sm:text-sm rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                  required=""
                  onChange={onchange}
                  value={credentials.password}
                />
              </div>
              <div>
                <label
                  for="Confirmpassword"
                  className="block mb-2 text-sm font-medium text-gray-900 dark:text-white"
                >
                  Confirm password
                </label>
                <input
                  type="password"
                  name="confirmpassword"
                  id="confirmpassword"
                  placeholder="••••••••"
                  className="bg-gray-50 border border-gray-300 text-gray-900 sm:text-sm rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                  required=""
                  onChange={onchange}
                  value={credentials.cpassword}
                />
              </div>

              <div>
                <label
                  for="Address"
                  className="block mb-2 text-sm font-medium text-gray-900 dark:text-white"
                >
                  Address
                </label>
                <input
                  type="text"
                  name="address"
                  id="address"
                  placeholder="Lane 2 xyz street"
                  className="bg-gray-50 border border-gray-300 text-gray-900 sm:text-sm rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                  required=""
                  onChange={onchange}
                  value={credentials.address}
                />
              </div>
              <div>
                <label
                  for="Contact"
                  className="block mb-2 text-sm font-medium text-gray-900 dark:text-white"
                >
                  Contact
                </label>
                <input
                  type="tel"
                  name="contact"
                  id="contact"
                  placeholder="+921234567890"
                  className="bg-gray-50 border border-gray-300 text-gray-900 sm:text-sm rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                  required=""
                  onChange={onchange}
                  value={credentials.contact}
                />
              </div>
              
              
              <button
                type="submit"
                className="w-full text-white bg-black hover:bg-primary-700 focus:ring-4 focus:outline-none focus:ring-primary-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-primary-600 dark:hover:bg-primary-700 dark:focus:ring-primary-800"
              >
                Create an account
              </button>
              <p className="text-sm font-light text-gray-500 dark:text-gray-400">
                Already have an account?{" "}
                <button onClick={SubmitLogin}><a className="font-medium text-primary-600 hover:underline dark:text-primary-500">Login here</a></button>
              </p>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Signup;
