import React, { useState } from 'react'
import {useForm} from 'react-hook-form'
import { useNavigate } from 'react-router-dom'
import { useEffect } from 'react';
import editlogo from './icons8-edit-50.png'
import editlogogif from './icons8-edit.gif'
function Edit() {
  const [alertMessage, setAlertMessage] = useState(null);
  const [editableField, setEditableField] = useState(null)
  const clearAlert = () => {
    setAlertMessage(null);
  };
  const showAlertMessage = (message) => {
    setAlertMessage(message);
    setTimeout(clearAlert, 5000); // Hide the alert after 5 seconds
  };
  const navigate = useNavigate()
  const { watch, register,handleSubmit,setError,setValue, formState:{errors, isValid}, getValues }= useForm({mode:'all'})


  const handleEditClick = (fieldName) => {
    setEditableField(fieldName);
  };

  const handleSaveClick = () => {
    setEditableField(null);
  };
  
  const handledeleteclick = async ()=>{
    const isConfirmed = window.confirm("Are you sure you want to delete your account?")
    if (isConfirmed) {
    const email = localStorage.getItem('email')
    const res = await fetch(`http://localhost:5000/api/auth/fetchuserdata/${email}`, {
      method : 'GET',
      headers : {
        "Content-Type": "application/json"
      }
    })
    const data = await res.json()
    console.log(data)
    const {customer_id} = data
    const deleteuser = await fetch("http://localhost:5000/api/auth/deleteuser",{
      method : 'POST',
      headers : {
        "Content-Type": "application/json"
      },
      body : JSON.stringify({
        customer_id : customer_id
      })
    })
    if (deleteuser.ok){
      console.log('Successfully deleted the user')
      localStorage.removeItem('email')
      localStorage.removeItem('name')
      localStorage.removeItem('authtoken')
      navigate('/')
      showAlertMessage('User deleted successfully')
    }
  }else{
    console.log('Not deleted the user')
  }
}

useEffect(() => {
  const fetchData = async () => {
    try {
      const email = localStorage.getItem('email');
      const res = await fetch(`http://localhost:5000/api/auth/fetchuserdata/${email}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      });
      const data = await res.json();

      // Use setValue to populate form fields with fetched data
      setValue('fullname', data.name);
      setValue('address', data.address);
      setValue('contact', data.contact);
      setValue('password', data.password)

      // Note: If there are more fields, update them similarly

    } catch (error) {
      console.error('Error fetching user details:', error);
      // Handle error (show alert, redirect, etc.)
    }
  };

  fetchData(); // Call the fetchData function when the component mounts
}, []);


const handleupdateclick = async (e)=>{
  const values = getValues()
  e.preventDefault()
  const isConfirmed = window.confirm('Are you sure you want to update account settings ?')
  if (isConfirmed)
  {
    const email = localStorage.getItem('email')
    // const restwo = await fetch(`http://localhost:5000/api/auth/fetchuserdata/${email}`, {
    //   method : 'GET',
    //   headers : {
    //     "Content-Type": "application/json"
    //   }
    // })
    // const data = await restwo.json()
    // console.log(data)
    const res = await fetch('http://localhost:5000/api/auth/updateuser',{
      method : 'POST',
      headers : {
        "Content-Type": "application/json"
      },
      body : JSON.stringify({
        email : email,
        password : values.password,
        contact : values.contact,
        address : values.address ,
        name : values.fullname,
      })
    })
    const details =  res
    if (details.ok)
    {
      console.log('Updated settings successfully')
      localStorage.removeItem('name')
      localStorage.setItem('name', values.fullname)
      navigate('/')
      showAlertMessage('Settings updated successfully');
    }
    else{
      console.log('Error updating settings')
    }
  }
  else{
    console.log('Settings not updated')
  }
}
  return (
    <>

    {alertMessage && (
      <div className="fixed top-0 left-0 right-0 bg-green-500 text-white p-4 text-center">
        {alertMessage}
      </div>
    )}


    <div className="h-[150px] h-[200px] bg-gray-700 flex justify-center items-center">
    <h className="md:text-[30px] text-[19px] text-white no">Account Settings</h>
  </div>
  <div className='md:mx-10 mb-5 mx-2 mt-10'></div>
    <div className="min-h-screen  flex flex-col items-start text-gray-900 antialiased relative">

    <div
        style={{
          clipPath: "polygon(0 0, 100% 0, 100% 80%, 0% 100%)",
          height: "34rem",
          backgroundColor : '#374151'
        }}
        className="absolute  inset-x-0 top-0"
      ></div>
      <div className="mx-auto z-10 mt-48 text-center">
        <h1 className="text-white text-4xl font-semibold no">
          Update / Delete Account
        </h1>
       
      </div>
      <div className="max-w-xl w-full mt-24 mb-24 rounded-lg shadow-2xl bg-white mx-auto overflow-hidden z-10">
      <div className="px-16 py-10">
      <form>
      <h2 className="font-bold text-2xl mb-8 my-4 text-center">
              Update Profile
        </h2>
        <div className="relative z-0 w-full mb-5 group flex items-center">
            <input type="text" name="fullname" onClick={()=>handleEditClick('fullname')} id="fullname" className="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer" readOnly={editableField !== 'fullname'} placeholder=" " required { ...register('fullname', {
              required: 'Full Name cannot be blank',
            })}/>
            {!editableField && (
                  <button
                    type="button"
                    className="ml-2 text-blue-600"
                    onClick={() => handleEditClick('fullname')}
                  >
                    <img src={editlogogif} alt="editlogo" width={20} height={20} />
                  </button>
                )}
                {editableField === 'fullname' && (
                  <>
                    <button
                      type="button"
                      className="ml-2 text-blue-600"
                      onClick={() => handleSaveClick()}
                    >
                      Save
                    </button>
                    <button
                      type="button"
                      className="ml-2 text-red-600"
                      onClick={() => setEditableField(null)}
                    >
                      Cancel
                    </button>
                  </>
                )}
            
            <label for="fullname" className="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-3 -z-10 origin-[0] peer-focus:start-0 rtl:peer-focus:translate-x-1/4 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">Full Name</label>
          </div>
          {errors.fullname && <p className='text-red-600 text-sm mb-4'>{errors.fullname.message}</p>}

          <div className="relative z-0 w-full mb-5 group flex items-center">
            <input type="text" name="address"   id="address"  onClick={()=>handleEditClick('address')} className="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer" placeholder=" " required {...register('address',{
              required:'Address cannot be blank'
            })}/>
            {!editableField && (
                  <button
                    type="button"
                    className="ml-2 text-blue-600"
                    onClick={() => handleEditClick('address')}
                  >
                    <img src={editlogogif} alt="editlogo" width={20} height={20} />
                  </button>
                )}
                {editableField === 'address' && (
                  <>
                    <button
                      type="button"
                      className="ml-2 text-blue-600"
                      onClick={() => handleSaveClick()}
                    >
                      Save
                    </button>
                    <button
                      type="button"
                      className="ml-2 text-red-600"
                      onClick={() => setEditableField(null)}
                    >
                      Cancel
                    </button>
                  </>
                )}
           
            <label htmlFor="address" className="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-3 -z-10 origin-[0] peer-focus:start-0 rtl:peer-focus:translate-x-1/4 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">Address</label>
          </div>
          {errors.address && <p className='text-red-600 text-sm mb-4'>{errors.address.message}</p>}
          <div className="relative z-0 w-full mb-5 group flex items-center">
            <input type="text" name="contact"  id="contact"  onClick={()=>handleEditClick('contact')} className="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer" placeholder=" " required {...register('contact',{
              required:'Contact cannot be blank and must begin with +92',
              maxLength : {
                value : 13,
                message : 'Value cannot be greater than 13'
              },
              pattern : /^\+92\d{10}$/g,
              minLength:{
                  value : 13,
                  message : 'Value cannot be less than 13'
              }
            })} />
            {!editableField && (
                  <button
                    type="button"
                    className="ml-2 text-blue-600"
                    onClick={() => handleEditClick('contact')}
                  >
                    <img src={editlogogif} alt="editlogo" width={20} height={20} />
                  </button>
                )}
                {editableField === 'contact' && (
                  <>
                    <button
                      type="button"
                      className="ml-2 text-blue-600"
                      onClick={() => handleSaveClick()}
                    >
                      Save
                    </button>
                    <button
                      type="button"
                      className="ml-2 text-red-600"
                      onClick={() => setEditableField(null)}
                    >
                      Cancel
                    </button>
                  </>
                )}
            
            <label htmlFor="contact" className="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-3 -z-10 origin-[0] peer-focus:start-0 rtl:peer-focus:translate-x-1/4 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">Contact No</label>
          </div>
          {errors.contact && <p className='text-red-600 text-sm mb-4'>{errors.contact.message}</p>}

          <div className="relative z-0 w-full mb-5 group flex items-center">
            <input type="password" name="password"  onClick={()=>handleEditClick('password')} id="password" className="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer"  placeholder=" " required {...register('password',{
              required:'Password cannot be blank',
              minLength : {
                value : 8,
                message : 'Password must be atleast 8 chracters long'
              }
            })}/>
            {!editableField && (
                  <button
                    type="button"
                    className="ml-2 text-blue-600"
                    onClick={() => handleEditClick('password')}
                  >
                    <img src={editlogogif} alt="editlogo" width={20} height={20} />
                  </button>
                )}
                {editableField === 'password' && (
                  <>
                    <button
                      type="button"
                      className="ml-2 text-blue-600"
                      onClick={() => handleSaveClick()}
                    >
                      Save
                    </button>
                    <button
                      type="button"
                      className="ml-2 text-red-600"
                      onClick={() => setEditableField(null)}
                    >
                      Cancel
                    </button>
                  </>
                )}
           
            <label htmlFor="address" className="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-3 -z-10 origin-[0] peer-focus:start-0 rtl:peer-focus:translate-x-1/4 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">Password</label>
          </div>
          {errors.password && <p className='text-red-600 text-sm mb-4'>{errors.password.message}</p>}
          <button className='mt-6 bg-black hover:bg-gray-600 text-white  rounded-full px-8 py-6 text-sm  disabled:bg-gray-400 disabled:cursor-not-allowed float-right mb-3' onClick={handledeleteclick}>Delete Account</button>
          <button className='mt-6 bg-black hover:bg-gray-600 text-white  rounded-full px-8 py-6 text-sm  disabled:bg-gray-400 disabled:cursor-not-allowed float-left mb-3' disabled = {!isValid} onClick={handleupdateclick}>Update Account</button>
          
      </form>
      </div>
      </div>

    </div>
    </>
  )
}

export default Edit