// ReviewForm.js
import React, { useContext, useState } from 'react';
import classNames from 'classnames';
import userdetailscontext from './context/userdetailscontext';
const ReviewForm = () => {
  const [review,setreview] = useState()
  const {userloggedindetails} = useContext(userdetailscontext)
  const [isFormOpen, setIsFormOpen] = useState(false);
  const [rating, setRating] = useState(0);
  const [hoveredRating, setHoveredRating] = useState(0);
  const [showSuccessAlert, setShowSuccessAlert] = useState(false);
  const onChange = (e)=>{
    setreview(e.target.value)
  }
  const handlesubmit = async(e)=>{
    e.preventDefault()
    try{
      let email = localStorage.getItem('email')
          let res = await fetch(`http://localhost:5000/api/auth/fetchuserdata/${email}`)
          const details = await res.json()
          console.log(details)
    const storedata = await fetch('http://localhost:5000/api/review/userreview',{
      method : 'POST',
      headers: {
        "Content-Type": "application/json",
      },
      body : JSON.stringify({
        customer_id : details.customer_id,
        review : review
      })
    })
    console.log(userloggedindetails.customer_id, review)
    if(!storedata.ok)
    {
      console.log('Error storing review data', storedata.statusText)
      return
    }
    const sentdata = await storedata.json()
  console.log('data sent for stroing in review table is ', sentdata)
    setShowSuccessAlert(true)
    setreview('')
    setRating(0)
    setTimeout(() => {
      setShowSuccessAlert(false)
    },3000 );
    }catch(error){console.log('error occured')}
  }
  const toggleForm = () => {
    setIsFormOpen(!isFormOpen);
  };

  const handleStarHover = (hoveredRating) => {
    setHoveredRating(hoveredRating);
  };

  const handleStarClick = () => {
    setRating(hoveredRating);
  };

  const handleStarLeave = () => {
    setHoveredRating(0);
  };

  return (
    <div className="fixed top-1/2 right-0 transform -translate-y-1/2 m-4">
      <button
        className={classNames(
          ' no bg-gradient-to-r from-yellow-500 to-yellow-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline flex items-center transition-all duration-500 opacity-100 hover:opacity-90',
          {
            'ml-2': isFormOpen,
            'mr-2': !isFormOpen,
            'opacity-0': isFormOpen,
          }
        )}
        onClick={toggleForm}
      >
        {isFormOpen ? (
          <>
            <span>Close</span>
            <span className="ml-2">&larr;</span>
          </>
        ) : (
          <>
            <span>Review</span>
            <span className="ml-2">&rarr;</span>
          </>
        )}
      </button>

      
        {showSuccessAlert && (<div className=" no top-4 mt-3 left-0 right-0 bg-black text-white font-bold py-3 px-4 text-center">
          <span role="img" aria-label="success-icon" className="mr-2">
            
          </span>
          Your feedback has been recorded !
        </div>)}
      

      {isFormOpen && (
        <form  className={classNames('mt-4 bg-gradient-to-r from-gray-100 to-white p-6 rounded-md shadow-lg transition-all duration-500 opacity-100 scale-100', {
          'scale-95': !isFormOpen,
        })}>
          {/* Add your form fields and logic here */}
          <div className="mb-4">
            <label className=" no block text-gray-700 text-lg font-bold mb-2" htmlFor="review">
              Your Review:
            </label>
            <textarea
              className=" no resize-none border rounded w-full py-2 px-3 focus:outline-none focus:ring focus:border-blue-300"
              id="review"
              name="review"
              rows="4"
              value={review}
              onChange={onChange}
            ></textarea>
          </div>

          <div className="mb-4">
            <label className=" no block text-gray-700 text-lg font-bold mb-2" htmlFor="rating">
              Rating:
            </label>
            <div
              className="flex"
              onMouseLeave={handleStarLeave}
            >
              {[1, 2, 3, 4, 5].map((star) => (
                <span
                  key={star}
                  className={classNames('cursor-pointer text-gray-300', {
                    'text-yellow-500': star <= (hoveredRating || rating),
                  })}
                  onMouseEnter={() => handleStarHover(star)}
                  onClick={handleStarClick}
                >
                  <i className="fa-solid fa-star px-1"></i>
                </span>
              ))}
            </div>
          </div>

          {/* Add more form fields as needed */}

          <button
            type='submit'
            onClick={handlesubmit}
            className=" no bg-black hover:bg-gray-600 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
          >
            Submit Review
          </button>
        </form>
      )}
    </div>
  );
};

export default ReviewForm;
