// src/components/Dropdown.js
import React, { useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import userdetailscontext from './context/userdetailscontext';
const Dropdown = () => {
  const {userloggedindetails} = useContext(userdetailscontext)
  const [isOpen, setIsOpen] = useState(false);

  const navigate = useNavigate();
  const handlelogout = ()=>{
    localStorage.removeItem('authtoken')
    localStorage.removeItem('name')
    localStorage.removeItem('email')
    navigate('/')
    setIsOpen(false)
  }

  const toggleDropdown = () => {
    setIsOpen(!isOpen);
  };
  const handleviewprofile = ()=>{
    navigate('/editprofile')
    setIsOpen(false)
  }

  return (
    <div className="relative inline-block text-left">
      <button
        type="button"
        onClick={toggleDropdown}
        className=" no inline-flex font-semibold items-center px-3 py-2 border border-transparent rounded-md shadow-sm  text-white"
      >
        <svg
          className="-ml-1 mr-2 h-5 w-5"
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth="2"
            d="M4 6h16M4 12h16m-7 6h7"
          />
        </svg>
         Hi , {localStorage.getItem('name')}
      </button>

      {isOpen && (
        <div className="z-10 origin-top-right absolute right-0 mt-2 w-56 rounded-md shadow-lg bg-white ring-1 ring-black ring-opacity-5">
          <div className="py-1" role="menu" aria-orientation="vertical" aria-labelledby="options-menu">
            {/* Add your dropdown items here */}
            <button
            onClick={handleviewprofile}
              href="#"
              className=" no block px-4 py-2 font-semibold text-gray-800 hover:bg-gray-100 transition duration-300 ease-in-out"
              role="menuitem"
            >
              Account Settings
              </button>
            <button
              onClick={handlelogout}
              className=" no block px-4 py-2  font-semibold  text-gray-800 hover:bg-gray-100 transition duration-300 ease-in-out"
              role="button"
            >
              Logout
              </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default Dropdown;
