import React from "react";
import hashlogo from './pngghh.png'
import { Route,Routes,useNavigate } from "react-router-dom";
import { useState } from "react";
import { Link } from "react-router-dom";
import { useEffect, useContext } from "react";
import userdetailscontext from "./context/userdetailscontext";
function Login() {


  const {setuserloggedindetails} = useContext(userdetailscontext)
  const [credentials,setcredentials] = useState({
    email : "",
    password : ""
  })
  const navigate = useNavigate();
  const FetchUser = async()=>{
    const res = await fetch("http://localhost:5000/api/auth/fetchuser",{
      method: 'POST',
      headers : {
        "Content-Type": "application/json",
        "auth-token": localStorage.getItem("authtoken"),
      }
    })
  }

  const Fetchuserdata = async()=>{
    const res = await fetch(`http://localhost:5000/api/auth/fetchuserdata/${credentials.email}`,{
      method : 'GET',
      headers : {
        "Content-Type": "application/json"
      }
    }).then(response => response.json()).then(data => {
      const {name, email, customer_id, password, contact, address} = data
      localStorage.setItem('name',name)
      localStorage.setItem('email',email)
      setuserloggedindetails({
        email : email,
        password : password,
        customer_id : customer_id,
        address : address,
        contact : contact,
        name : name
      })
    })
  }
  const HandleSubmit = async(e)=>{
    e.preventDefault()
    const response = await fetch("http://localhost:5000/api/auth/login", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        email: credentials.email,
        password: credentials.password,
      }),
    });
    const json = await response.json();
    console.log(json);
    if (json.success) {
      // save the auth token and redirect
      localStorage.setItem("authtoken", json.authtoken);
        FetchUser()
       const data =  await Fetchuserdata()
       console.log(data)
       navigate('/')
    } else {
      alert("Enter valid credentials");
    }
  }
    
  
  const OnChange = async(e)=>{
    setcredentials({ ...credentials, [e.target.name] : e.target.value})
  }
  const submitRegister = ()=>{
    navigate('../signup')
  }
  return (
    <section className="gradient-form h-full bg-neutral-200 dark:bg-neutral-700">
      <div className=" h-full p-10">
        <div className="g-6 flex h-full flex-wrap items-center justify-center text-neutral-800 dark:text-neutral-200">
          <div className="w-full">
            <div className="block rounded-lg bg-white shadow-lg dark:bg-neutral-800">
              <div className="g-0 lg:flex lg:flex-wrap">
                <div className="px-4 md:px-0 lg:w-6/12">
                  <div className="md:mx-6 md:p-12">
                    <div className="text-center">
                      <img
                        style={{}}
                        className="mx-auto w-48"
                        src={hashlogo}
                        alt="logo"
                      />
                      <h4 className="mb-12 mt-1 pb-1 text-xl font-semibold">
                        Join our Elite club!
                      </h4>
                    </div>

                    <form onSubmit={HandleSubmit}>
                      <p className="mb-4">Please login to your account</p>

                      <div className="relative mb-4" data-te-input-wrapper-init>
                        <input
                          type="email"
                          className="peer block min-h-[auto] w-full rounded border-0 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 data-[te-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:placeholder:text-neutral-200 [&:not([data-te-input-placeholder-active])]:placeholder:opacity-0"
                          id="email"
                          placeholder="email"
                          name="email"
                          onChange={OnChange}
                          value={credentials.email}
                        />
                        <label
                          for="exampleFormControlInput1"
                          className="pointer-events-none absolute left-3 top-0 mb-0 max-w-[90%] origin-[0_0] truncate pt-[0.37rem] leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.9rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[te-input-state-active]:-translate-y-[0.9rem] peer-data-[te-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-200 dark:peer-focus:text-primary"
                        >
                          Email
                        </label>
                      </div>

                      <div className="relative mb-4" data-te-input-wrapper-init>
                        <input
                          type="password"
                          className="peer block min-h-[auto] w-full rounded border-0 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 data-[te-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:placeholder:text-neutral-200 [&:not([data-te-input-placeholder-active])]:placeholder:opacity-0"
                          id="password"
                          placeholder="Password"
                          name="password"
                          onChange={OnChange}
                          value={credentials.password}
                        />
                        <label
                          for="exampleFormControlInput11"
                          className="pointer-events-none absolute left-3 top-0 mb-0 max-w-[90%] origin-[0_0] truncate pt-[0.37rem] leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.9rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[te-input-state-active]:-translate-y-[0.9rem] peer-data-[te-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-200 dark:peer-focus:text-primary"
                        >
                          Password
                        </label>
                      </div>

                      <div className="mb-12 pb-1 pt-1 text-center">
                        <button
                          className="mb-3 inline-block w-full rounded px-6 pb-2 pt-2.5 text-xs font-medium uppercase leading-normal text-white shadow-[0_4px_9px_-4px_rgba(0,0,0,0.2)] transition duration-150 ease-in-out hover:shadow-[0_8px_9px_-4px_rgba(0,0,0,0.1),0_4px_18px_0_rgba(0,0,0,0.2)] focus:shadow-[0_8px_9px_-4px_rgba(0,0,0,0.1),0_4px_18px_0_rgba(0,0,0,0.2)] focus:outline-none focus:ring-0 active:shadow-[0_8px_9px_-4px_rgba(0,0,0,0.1),0_4px_18px_0_rgba(0,0,0,0.2)]"
                          type="submit"
                          data-te-ripple-init
                          data-te-ripple-color="light"
                          style={{
                            background:
                              "linear-gradient(to right, #374151, #374151d1, #3741519c, #3741517a)",
                          }}
                        >
                          Log in
                        </button>

                        <a href="#!">Forgot password?</a>
                      </div>

                      <div className="flex items-center justify-between pb-6">
                        <p className="mb-0 mr-2">Don't have an account?</p>
                        <button
                          type="button"
                          className="inline-block rounded border-2 border-danger px-6 pb-[6px] pt-2 text-xs font-medium uppercase leading-normal text-danger transition duration-150 ease-in-out hover:border-danger-600 hover:bg-neutral-500 hover:bg-opacity-10 hover:text-danger-600 focus:border-danger-600 focus:text-danger-600 focus:outline-none focus:ring-0 active:border-danger-700 active:text-danger-700 dark:hover:bg-neutral-100 dark:hover:bg-opacity-10"
                          data-te-ripple-init
                          data-te-ripple-color="light"
                          onClick={submitRegister}
                        >
                          Register
                        </button>
                      </div>
                    </form>
                  </div>
                </div>

                <div
                  className="flex items-center rounded-b-lg lg:w-6/12 lg:rounded-r-lg lg:rounded-bl-none"
                  style={{
                    background:
                      "linear-gradient(to right, #374151, #374151d1, #3741519c, #3741517a)",
                  }}
                >
                  <div className="px-4 py-6 text-white md:mx-6 md:p-12">
                    <h4 className="mb-6 text-xl font-semibold">
                      Benefits of joining
                    </h4>
                    <p className="text-sm">
                      Join our rent-a-car club for exclusive benefits, including
                      cost savings through discounts and special offers,
                      priority service for a seamless rental experience,
                      flexible options with a diverse vehicle fleet, rewarding
                      points system for future discounts, and exclusive access
                      to promotions and events. Enjoy 24/7 customer support for
                      added peace of mind.
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Login;
